

.Text{
  align-items: center;
  text-align: center;
  margin-top: 5vh;
  margin-left: 25%;
  max-width: 50%;
  color: #282c34;
  text-shadow: 1px 1px 4px rgb(192, 192, 192);


}
.Card {
  background-color: #282c34;
  min-height: 25vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
  padding-bottom: 0px;
  text-shadow: 1px 1px 1px rgb(255, 255, 255);
}

.App-link {
  color: #61dafb;
}

.wrapper {
  display: flex;
  margin-top: 5vh;
  height: 100vh;
  width: 100vw;
  justify-content: center;
}

.accordion {
  width: 500px;
}

.item {
  background: #747f96;
  margin-bottom: 5px;
  padding: 10px 20px;
  border: black;
  border-width: 2px;
}

.item:hover {
  background: #8896b3;
  margin-bottom: 5px;
  padding: 10px 20px;
}

.title {
  color: white;
  display: flex;
  justify-content: space-between;
  align-items: center;
  cursor: pointer;
  user-select: none;
  text-shadow: 1px 1px 4px rgb(64, 72, 110);

}

.content {
  max-height: 0;
  overflow: hidden;
  transition: all 0.3s cubic-bezier(0,1,0,1);
  display: flex;
  user-select: none;
  text-align: justify;
  text-shadow: 0px 0px 2px rgb(102, 115, 153);
}

.content.show {
  height: auto;
  display: flex;
  max-height: 9999px;
  transition: all 0.3s cubic-bezier(1,0,1,0);
  user-select: none;
  text-align: justify;
  text-shadow: 0px 0px 2px rgb(100, 112, 145);
}

.link {
  align-items: center;
  text-align: center;
  text-shadow: 1px 1px 2px rgb(216, 216, 216);
  color:rgb(64, 72, 110);
  text-decoration: none;
}

.link:hover {
  color:rgb(102, 115, 153)
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

